/* eslint-disable react-native/no-inline-styles */
import React from "react";
import styled, { keyframes } from "styled-components";
import { Button } from "@mui/material";
import { useState } from "react";

const MobileEducationStyler = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  background-color: lightgray;
  overflow: scroll;
`;

const C1MobileEducationHeaderStyler = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  background-color: lightblue;
`;

const C1C1HeaderStyler = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  flex-grow: 1;
  background-color: rgb(169, 204, 222);
  text-align: center;
  align-items: center;
  justify-content: center;
`;

const C1C1C1HeaderText = styled.h2`
  padding: 1% 10%;
  background-color: white;
  border-radius: 10px;
`;

const C1C2ImageHolder = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  background-color: white;
  align-items: center;
`;

const C1C2C1UNCSplash = styled.img`
  display: flex;
  flex-direction: row;
  flex: 1;
  flex-grow: 1;
  background-color: white;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  object-fit: cover;
`;

const scrollAnimation = keyframes`
0% {
  background-position: 0 0;
}
50% {
  background-position: 100% 100%;
}
100% {
  background-position: 0 0;
}
`;

const C2Content = styled.div`
  display: flex;
  flex: 1;
  flex-grow: 20;
  flex-direction: column;
  background-image: url("https://i.ibb.co/wBv9sTN/unc-campus.jpg");
  animation: ${scrollAnimation} 200s linear infinite;
`;

const C2C1Academia = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  background-color: rgb(255, 255, 255, 0.9);
  margin: 3%;
  border-radius: 10px;
  max-height: 80vh;
`;

const C2C1C1AcademiaHeader = styled.h3`
  display: flex;
  flex: 1;
  flex-grow: 1;
  border: 1px solid rgb(100, 100, 100, 0.4);
  max-height: 4vh;
  justify-content: center;
  align-items: center;
`;

const C2C1C2AcademiaSummary = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  margin: 3%;
  border-radius: 10px;
  border: 1px solid rgb(100, 100, 100, 0.4);
  padding: 3%;
  overflow: scroll;
  max-height: 80%;
`;

const C2C2Courses = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  background-color: rgb(255, 255, 255, 0.9);
  margin: 3%;
  border-radius: 10px;
`;

const C2C2C1CoursesHeader = styled.h3`
  display: flex;
  flex: 1;
  flex-grow: 1;
  border: 1px solid rgb(100, 100, 100, 0.4);
  max-height: 4vh;
  justify-content: center;
  align-items: center;
`;

const C2C2C2CoursesSummary = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  margin: 3%;
  border-radius: 10px;
  border: 1px solid rgb(100, 100, 100, 0.4);
  padding: 3%;
  overflow: scroll;
  min-height: 80vh;
`;

export const MobileUnc = () => {
  const contentArray = [
    `|Comp 550: Algorithms and Analysis|
          \nIn Comp 550: Algorithms and Analysis, I honed my skills in designing and analyzing computer algorithms. The course emphasized problem-solving skills and formal thinking, covering topics such as algorithm analysis, asymptotic notation, divide and conquer, greedy algorithms, dynamic programming, and graph algorithms. Through this range of topics, I gained a deep understanding of core algorithms and their applications in real-world scenarios. I developed the ability to evaluate algorithm correctness and runtime performance using techniques like asymptotic notation and proof methods. By completing this course, I acquired a strong foundation in algorithmic problem-solving. This foundation equips me with the skills necessary to approach complex challenges and demonstrate expertise in algorithm analysis and design.
          \nPrimary Skills Acquired:
          \n - Algorithm Analysis
          \n - Asymptotic Notation
          \n - Divide and Conquer
          \n - Greedy Algorithms
          \n - Dynamic Programming
          \n - Graph Algorithms`,
    `|Comp 210: Data Structures and Analysis|
          \nIn this class, I mastered Java programming, data structures, and the "Object Oriented Programming" paradigm. I learned about Java syntax, abstract data types (ADTs), and memory management. Key topics included recursion, lists, stacks, queues, and binary trees. I also gained insight into algorithm efficiency using “Big Oh” notation. Sorting algorithms like insertion sort, selection sort, heap sort, merge sort, and quicksort were covered. Graph theory concepts, traversal methods, and essential algorithms were explored. Additionally, I studied abstract data types for sets and maps, as well as hash tables. I am proud to have gained valuable skills in problem-solving, algorithm analysis, and designing efficient solutions. My proficiency in Java programming has enabled me to develop complex applications with ease. I am confident that my knowledge of data structures and algorithms will be an asset to any team that I work with.
          \nPrimary Skills Acquired:
          \n - Java Programming
          \n - Abstract Data Types (ADTs)
          \n - Memory Management
          \n - Algorithm Efficiency
          \n - Sorting Algorithms
          \n - Graph Theory
          \n - Hash Tables`,
    `|Comp 301: Foundations of Programming|
        \nDuring the course, I had the opportunity to enhance my proficiency in Java programming to an advanced level. Through comprehensive study, I acquired a deep understanding of crucial concepts such as encapsulation, inheritance, polymorphism, mutability, abstraction, and error handling. My knowledge was further consolidated by rigorous JUnit testing, ensuring the production of robust and error-free code. Additionally, I delved into the implementation of essential design patterns, including Iterator, Decorator, Singleton, Multiton, and Factory, which proved instrumental in achieving optimal software architecture. Furthermore, I assimilated the Model-View-Controller (MVC) design pattern, a pivotal framework for developing scalable and maintainable applications. Demonstrating adaptability, I explored the realm of asynchronous programming and threading, honing my skills in efficient multitasking. Overall, the invaluable experience gained from COMP 301 equipped me with solid software design principles and has given me the ability to create flexible and maintainable code.
        \n Primary Skills Acquired:
        \n - Java Programming Refined
        \n - JUnit Testing
        \n - Design Patterns
        \n - Model-View-Controller (MVC) Design Pattern
        \n - Asynchronous Programming and Threading`,
    `|Comp 426: Modern Web Programming|
        \nIn the course, we adopted a practical and hands-on approach to mastering the design, construction, and interaction with web APIs, back-end server applications, and front-end client apps and interfaces. Alongside technical skills, we primarily emphasized on the development of "soft skills" that are often overlooked but crucial in the field. This included collaborative development workflows, project management, error comprehension, documentation reading and writing, and maintaining healthy work practices.
        \nPrimary Skills Acquired:
        \n - Java Script
        \n - Node JS
        \n - Express JS
        \n - REST
        `,
    `|Comp 590: Ubiquitous Systems and Intelligent Sensing|
        \nThroughout this course, I achieved significant milestones that attest to my practical expertise and profound knowledge in this field. I successfully integrated a sensor with my computer, establishing flawless communication by implementing GET and POST requests to a MongoDB server. This hands-on endeavor provided invaluable insights into the mechanisms of data exchange and storage. Furthermore, I actively contributed to a compelling project where I developed an Amazon Alexa application, harnessing its capabilities to intelligently report and analyze sensor data that was posted to the MongoDB server. This project showcased my ability to leverage cutting-edge technologies and synthesize knowledge to create comprehensive systems that bridge the gap between ubiquitous systems and intelligent sensing. Through Comp 590, I have honed my skills in designing and implementing sophisticated solutions, unlocking new possibilities in the realm of ubiquitous computing and intelligent sensing.
        \nPrimary Skills Acquired:
        \n - Arduino Programming
        \n - Amazon Alexa Skills Kit
        \n - Python Skills Refined
        \n - MongoDB
        \n - Get and Post Requests`,
    `|Comp 562: Intro to Machine Learning|
        \nI completed this course that provided me with a comprehensive understanding of the fundamental concepts of machine learning. The course covered various topics such as linear regression, logistic regression, and neural networks. It also included support vector machines, decision trees, and ensemble methods.`,
    `|Comp 211: Systems Fundamentals|
        \nFor this class, I undertook a comprehensive exploration of the essential concepts that underpin computer systems. Through rigorous study and practical application, I developed a deep understanding of the fundamental principles and practices within the realm of computing. Immersing myself in the intricacies of the C programming language, I delved into advanced topics including pointers, arrays, strings, and memory management, acquiring the skills necessary to craft efficient and optimized code. This course equipped me with the ability to solve complex problems, analyze functions, and precisely model real-world phenomena. With this robust foundation, I am now poised to navigate the dynamic landscape of computer systems and contribute to their advancement in a professional capacity.
        \n|Comp 311: Computer Organization|
        \n This course had me explore the intricate layers that constitute the architecture of digital systems. I gained a comprehensive understanding of CPU organization, memory management, and I/O systems, unraveling the intricacies of efficient and high-performing computing. Additionally, I delved into the realm of instruction set architecture, addressing modes, and assembly language programming, enabling me to optimize code at a lower level.
        \nPrimary Skills Acquired:
        \n - C Programming
        \n - Assembly Language
        \n - Instruction Set Architecture
        \n - Addressing Modes
        \n - Memory Management`,
    `|Comp 455: Models of Languages and Computation|
        \nFor this course, I gained a comprehensive understanding of various fundamental concepts in computer science. Throughout the course, we delved into finite representations of languages and explored deterministic and nondeterministic finite automata. We also studied the relationship between finite automata and regular expressions, and learned techniques for demonstrating that certain languages are non-regular. Furthermore, we examined context-free grammars and their applications in parsing, along with push-down automata and context-free languages. The course covered Turing machines, their extensions, and the concepts of acceptance, rejection, and I/O for Turing machines. We also explored the limits of computability through undecidability and the halting problem. Through this course, I honed my skills in language theory, automata, grammars, and problem-solving, enabling me to tackle complex computational challenges with confidence and precision.
        \n|Comp 283: Discrete Structures|
        \nThis class primarily served to teach me discrete mathematics and its applications in Computer Science. I learned about propositional logic, predicate logic, and their applications in computer science. The course also covered set theory, relations, functions, and their applications. I developed a deep understanding of discrete mathematics and its applications in real-world scenarios, equipping me with the tools to solve complex problems, analyze functions, and model real-world phenomena with mathematical precision.`,
    `|Math 231-233: Calculus 1, 2, and 3|
        \nCalculus at UNC covered topics such as limits, derivatives, and integrals, providing a comprehensive understanding of the elementary transcendental functions. I learned various techniques of integration, including integration by parts, substitution, and partial fractions. We explored indeterminate forms and delved into Taylor's formula, which allowed for precise approximations of functions using polynomials. Infinite series were extensively studied, with a focus on convergence tests and their applications in real-world scenarios. The course also extended the principles of calculus to functions of several variables, introducing concepts such as vector algebra, solid analytic geometry, partial derivatives, and multiple integrals. I developed a deep understanding of calculus and its broad applications, equipping me with the tools to solve complex problems, analyze functions, and model real-world phenomena with mathematical precision.`,
    `|Math 347: Linear Algebra|
        \nThroughout this course, I learned about vector spaces, linear transformations, matrices, determinants, eigenvalues, and eigenvectors. The course also covered inner product spaces, orthogonality, and the Gram-Schmidt process.
        \n|Stor 435: Introduction to Probability|
        \nThis course provided me with a comprehensive grasp of probability theory and its practical applications. I gained a deep understanding of probability spaces, random variables, distributions, and expected values. Exploring joint and conditional distributions, I learned to analyze relationships between variables in different scenarios. The course also emphasized the central limit theorem's significance in data analysis and statistical inference. Equipped with this knowledge, I am well-prepared to solve intricate problems, analyze functions, and model real-world phenomena accurately using probability theory.`,
    `|Comp 110: Introduction to Programming|
        \nIn this course, I gained a comprehensive understanding of programming and its applications. I learned about Python syntax, data types, and control structures. The course also covered functions, lists, and file input/output. This course provided me the basic understandings that would remain essential throughout the rest of my Computer Science journey.
        \nPrimary Skills Acquired:
        \n - Python Programming
        \n - Data Types
        \n - Control Structures
        \n - Functions
        \n - Lists
        \n - File Input/Output`,
    `|Stor 120: Foundations of Statistics and Data Science|
        \nFor my Data Science minor one class I took was Stor 120. I learned about descriptive statistics, probability, random variables, and probability distributions. The course also covered sampling distributions, confidence intervals, and hypothesis testing. We used Python primarily to analized data so with my Computer Science background so with my Computer Science background I was able to better acclimate to the new topics being tought in this course as well as better refining my Python skills.`,
    `|BONUS: Udemy - Complete React Native in 2023: Zero to Mastery (with Hooks)|
        \nThis course served as an excellent introduction to React Native and React, equipping me with the essential skills to build mobile and web applications. I learned the fundamentals of React Native and React, along with advanced topics like React hooks, React navigation, and animations. Through practical projects, I developed a mobile app for robot search and a restaurant review app integrated with Google Maps API. The hands-on experience gained from this course is invaluable, enabling me to create this portfolio site and fueling my enthusiasm for further exploration and learning in these dynamic technologies.
        \nPrimary Skills Acquired:
        \n - React Native
        \n - React
        \n - React Hooks
        \n - JavaScript
        \n - Expo
        `,
  ];

  const [currentIndex, setCurrentIndex] = useState(0);

  const handleLeftButtonClick = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === 0 ? contentArray.length - 1 : prevIndex - 1
    );
  };

  const handleRightButtonClick = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === contentArray.length - 1 ? 0 : prevIndex + 1
    );
  };

  const processedContent = contentArray[currentIndex]
    .split("\n")
    .map((line, index) => {
      const parts = line.split("|");
      const processedParts = parts.map((part, index) =>
        index % 2 === 1 ? <strong key={index}>{part}</strong> : part
      );

      return (
        <React.Fragment key={index}>
          {processedParts.map((part, index) => (
            <React.Fragment key={index}>{part}</React.Fragment>
          ))}
          <br />
        </React.Fragment>
      );
    });

  return (
    <MobileEducationStyler>
      <C1MobileEducationHeaderStyler>
        <C1C1HeaderStyler>
          <C1C1C1HeaderText>Education</C1C1C1HeaderText>
        </C1C1HeaderStyler>
        <C1C2ImageHolder>
          <C1C2C1UNCSplash
            src="https://i.ibb.co/YDZzNfg/unc.png"
            alt="unc"
            border="0"
          />
        </C1C2ImageHolder>
      </C1MobileEducationHeaderStyler>
      <C2Content>
        <C2C1Academia>
          <C2C1C1AcademiaHeader>Academia Summary</C2C1C1AcademiaHeader>
          <C2C1C2AcademiaSummary>
            <p>
              As a computer science student at UNC, I've gained valuable
              insights into problem-solving and critical thinking skills. I've
              learned to analyze complex issues and design efficient solutions,
              going beyond coding. Collaboration and teamwork have emphasized
              effective communication and adaptability.
              <br />
              <br />
              I've gained a versatile skill set with various programming
              languages and frameworks. I'm well-prepared to collaborate
              effectively and deliver high-quality software solutions. The
              field's ever-evolving nature has instilled a mindset of continuous
              learning and adaptability.
              <br />
              <br />
              My experience at UNC has provided a strong foundation, enhanced
              problem-solving abilities, and emphasized the value of
              collaboration and continuous learning.
            </p>
          </C2C1C2AcademiaSummary>
        </C2C1Academia>
        <C2C2Courses>
          <C2C2C1CoursesHeader>Courses Summary</C2C2C1CoursesHeader>
          <C2C2C2CoursesSummary>
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "space-between",
              }}
            >
              <Button
                onClick={handleLeftButtonClick}
                variant="contained"
                sx={{
                  margin: "1%",
                }}
              >
                {" "}
                {"<"}
              </Button>
              <h3
                style={{
                  justifyContent: "center",
                  alignItems: "center",
                  textAlign: "center",
                }}
              >
                Courses Summary
              </h3>
              <Button
                onClick={handleRightButtonClick}
                variant="contained"
                sx={{
                  margin: "1%",
                }}
              >
                {" "}
                {">"}
              </Button>
            </div>
            <p>{processedContent}</p>
          </C2C2C2CoursesSummary>
        </C2C2Courses>
      </C2Content>
    </MobileEducationStyler>
  );
};
